import React, { useEffect, useLayoutEffect, useRef, useState } from "react";
import {
  Alert,
  Avatar,
  FormControl,
  IconButton,
  InputAdornment,
  InputLabel,
  ListSubheader,
  MenuItem,
  Paper,
  Select,
  Snackbar,
  TextField,
  Tooltip,
  Typography,
} from "@mui/material";
import { useFormik } from "formik";
import { Box } from "@mui/system";
import EditIcon from "../Ordering/EditIcon";
import { makeStyles } from "@mui/styles";
import { useDispatch, useSelector } from "react-redux";
import {
  editData,
  getAccountData,
  setEditData,
} from "../../../redux/reducers/account";
import { useNavigate } from "react-router";
import { CustomMask } from "../../../components/CustomMask";
import { sendCode } from "../../../redux/reducers/auth";
import cookie from "cookie_js";

const Profile = ({ setLoading }) => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const { account, editedAccountData, regions } = useSelector(
    ({ account }) => account
  );

  const [disabled, setDisabled] = useState({
    fio: false,
    phone: false,
    place: false,
  });
  const [touched, setTouched] = useState(false);

  const handleChange = (event) => {
    var data = new FormData();
    data.append("avatar", event.target.files[0]);
    dispatch(editData(data));
    setLoading(true);
    dispatch(getAccountData());
  };

  const formik = useFormik({
    initialValues: {
      info: "",
      phone: "",
      district: "",
    },
    onSubmit: (values) => {
      values.phone = values.phone.replace(/[\s+().,-]/g, "");
      if (values.phone !== account.phone) {
        dispatch(
          sendCode({
            type: 3,
            phone: values.phone,
          })
        );
        cookie.set("phone", values.phone);
        navigate("/dashboard/profile/edit");
      }

      setLoading(true);
      dispatch(editData(values));

      setTimeout(() => {
        dispatch(getAccountData());
      }, 100);
    },
  });

  useEffect(() => {
    formik.setFieldValue("info", account?.info);
    formik.setFieldValue("phone", account?.phone);
    formik.setFieldValue("district", account?.district);
    // eslint-disable-next-line
  }, [account]);

  const firstUpdate = useRef(true);

  useLayoutEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    }
    setLoading(false);
    setTouched(false);
    // eslint-disable-next-line
  }, [editedAccountData]);

  const renderSelectGroup = (item) => {
    const items = item.district_set.map((district) => {
      return (
        <MenuItem
          sx={{
            fontWeight: "normal",
            pl: 4,
          }}
          key={district.id}
          value={district.id}
        >
          {district.name}
        </MenuItem>
      );
    });

    return [
      <ListSubheader
        sx={{
          fontWeight: "bold",
          color: "#000",
        }}
      >
        {item.name}
      </ListSubheader>,
      items,
    ];
  };

  return (
    <section className={classes.section}>
      <Typography variant="h3" mb={5}>
        Мой профиль
      </Typography>

      <Snackbar
        open={editedAccountData.open}
        autoHideDuration={2000}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
        onClose={() =>
          dispatch(setEditData({ ...editedAccountData, open: false }))
        }
      >
        <Alert
          // eslint-disable-next-line
          color={editedAccountData.severity == 200 ? "success" : "error"}
          variant="filled"
        >
          {
            // eslint-disable-next-line
            editedAccountData.severity != 200
              ? "Что-то пошло не так"
              : " Данные успешно изменены!"
          }
        </Alert>
      </Snackbar>

      <Paper
        sx={{
          background: "#FFFFFF",
          boxShadow: "0px 4px 58px rgba(0, 0, 0, 0.15)",
          borderRadius: "15px",
          maxWidth: 770,
          width: "100%",
          padding: { xs: 2.4, md: 5, lg: "50px 70px" },
        }}
      >
        <form onSubmit={formik.handleSubmit}>
          <Box display="flex">
            <div>
              <input
                type="file"
                id="upload"
                onChange={handleChange}
                accept="image/*"
                style={{ display: "none" }}
              />
              {!account?.avatar ? (
                <Avatar
                  sx={{ bgcolor: "#FFB951", mr: "15px" }}
                  className={classes.avatar}
                >
                  <i className="ri-user-3-fill" />
                </Avatar>
              ) : (
                <img className={classes.avatar} alt="" src={account?.avatar} />
              )}
              <Tooltip title="Изменить" component="label" htmlFor="upload">
                <IconButton>
                  <svg
                    width="36"
                    height="36"
                    viewBox="0 0 36 36"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <rect width="36" height="36" rx="14" fill="#FFB951" />
                    <path
                      d="M15.828 11L13.828 13H10V25H26V13H22.172L20.172 11H15.828ZM15 9H21L23 11H27C27.2652 11 27.5196 11.1054 27.7071 11.2929C27.8946 11.4804 28 11.7348 28 12V26C28 26.2652 27.8946 26.5196 27.7071 26.7071C27.5196 26.8946 27.2652 27 27 27H9C8.73478 27 8.48043 26.8946 8.29289 26.7071C8.10536 26.5196 8 26.2652 8 26V12C8 11.7348 8.10536 11.4804 8.29289 11.2929C8.48043 11.1054 8.73478 11 9 11H13L15 9ZM18 24C16.5413 24 15.1424 23.4205 14.1109 22.3891C13.0795 21.3576 12.5 19.9587 12.5 18.5C12.5 17.0413 13.0795 15.6424 14.1109 14.6109C15.1424 13.5795 16.5413 13 18 13C19.4587 13 20.8576 13.5795 21.8891 14.6109C22.9205 15.6424 23.5 17.0413 23.5 18.5C23.5 19.9587 22.9205 21.3576 21.8891 22.3891C20.8576 23.4205 19.4587 24 18 24ZM18 22C18.9283 22 19.8185 21.6313 20.4749 20.9749C21.1313 20.3185 21.5 19.4283 21.5 18.5C21.5 17.5717 21.1313 16.6815 20.4749 16.0251C19.8185 15.3687 18.9283 15 18 15C17.0717 15 16.1815 15.3687 15.5251 16.0251C14.8687 16.6815 14.5 17.5717 14.5 18.5C14.5 19.4283 14.8687 20.3185 15.5251 20.9749C16.1815 21.6313 17.0717 22 18 22Z"
                      fill="white"
                    />
                  </svg>
                </IconButton>
              </Tooltip>
              {/* <Tooltip title="Удалить">
                <IconButton onClick={deleteHandler}>
                  <svg
                    width="36"
                    height="36"
                    viewBox="0 0 36 36"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <rect width="36" height="36" rx="14" fill="#C4C4C4" />
                    <path
                      d="M13 10V8H23V10H28V12H26V27C26 27.2652 25.8946 27.5196 25.7071 27.7071C25.5196 27.8946 25.2652 28 25 28H11C10.7348 28 10.4804 27.8946 10.2929 27.7071C10.1054 27.5196 10 27.2652 10 27V12H8V10H13ZM12 12V26H24V12H12ZM15 15H17V23H15V15ZM19 15H21V23H19V15Z"
                      fill="white"
                    />
                  </svg>
                </IconButton>
              </Tooltip> */}
            </div>
            <Box
              sx={{
                ml: { xs: 2, md: 3 },
              }}
            >
              <Typography variant="h3">{account?.info}</Typography>
              <Typography variant="body2" mt={1}>
                {account?.phone}
              </Typography>
            </Box>
          </Box>

          <Box
            display="flex"
            mt={2}
            alignItems="center"
            sx={{
              flexDirection: { xs: "column", lg: "row" },
            }}
          >
            <Box sx={{ width: { xs: "100%", lg: "370px" } }}>
              <TextField
                fullWidth
                required={disabled.info}
                name="info"
                value={formik.values.info}
                onChange={formik.handleChange}
                disabled={!disabled.info}
                placeholder="Имя Фамилия"
                InputProps={{
                  endAdornment: (
                    <InputAdornment
                      position="end"
                      onClick={() => {
                        setTouched(true);
                        setDisabled({ ...disabled, info: true });
                      }}
                    >
                      <IconButton>
                        <EditIcon />
                      </IconButton>
                    </InputAdornment>
                  ),
                }}
                variant="outlined"
              />
              <TextField
                name="phone"
                value={formik.values.phone}
                onChange={formik.handleChange}
                disabled={!disabled.phone}
                sx={{ mt: 2 }}
                id="+996 (000) 00 00 00"
                fullWidth
                required
                placeholder="Номер телефона"
                InputProps={{
                  inputProps: {
                    inputMode: "tel",
                  },
                  inputComponent: CustomMask,
                  endAdornment: (
                    <InputAdornment
                      position="end"
                      onClick={() => {
                        setTouched(true);
                        setDisabled({ ...disabled, phone: true });
                      }}
                    >
                      <IconButton>
                        <EditIcon />
                      </IconButton>
                    </InputAdornment>
                  ),
                }}
                variant="outlined"
              />
              <FormControl
                fullWidth
                sx={{ mt: 2 }}
                onClick={() => setTouched(true)}
              >
                <InputLabel>Адрес места проживания</InputLabel>
                <Select
                  label="Адрес места проживания"
                  name="district"
                  required
                  value={formik.values.district}
                  onChange={formik.handleChange}
                >
                  {Array.isArray(regions?.data) &&
                    regions?.data?.map((item) => renderSelectGroup(item))}
                </Select>
              </FormControl>
            </Box>

            {touched && (
              <Box ml={4} sx={{ display: { xs: "flex", lg: "block" } }}>
                <IconButton type="submit">
                  <svg
                    width="60"
                    height="60"
                    viewBox="0 0 60 60"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <rect width="60" height="60" rx="8" fill="#FFB951" />
                    <path
                      d="M28.364 33.193L37.556 24L38.971 25.414L28.364 36.021L22 29.657L23.414 28.243L28.364 33.193Z"
                      fill="white"
                    />
                  </svg>
                </IconButton>
                <IconButton onClick={() => setTouched(false)}>
                  <svg
                    width="60"
                    height="60"
                    viewBox="0 0 60 60"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <rect width="60" height="60" rx="8" fill="#EDEDEF" />
                    <path
                      d="M30.0007 28.586L34.9507 23.636L36.3647 25.05L31.4147 30L36.3647 34.95L34.9507 36.364L30.0007 31.414L25.0507 36.364L23.6367 34.95L28.5867 30L23.6367 25.05L25.0507 23.636L30.0007 28.586Z"
                      fill="black"
                    />
                  </svg>
                </IconButton>
              </Box>
            )}
          </Box>
        </form>
      </Paper>
    </section>
  );
};

export default Profile;

const useStyles = makeStyles((theme) => ({
  avatar: {
    width: "100px!important",
    height: "100px!important",
    borderRadius: "35px",
    objectFit: "cover",
    marginBottom: 10,
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    fontSize: 70,
    transition: "all 0.3s ease-in-out",
    color: "#fff",
    [theme.breakpoints.down("md")]: {
      width: 80,
      height: 80,
      fontSize: 40,
    },
  },
  section: {
    "& svg": {
      cursor: "pointer",
    },
  },
}));
